import Vue from "vue";
import App from "./App.vue";
// import router from './router'
// import router plugin
import Router from "vue-router";

//import of other essentials for web operation
import BootstrapVue from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

import AOS from "aos"; // fade,etc animation effect
import "aos/dist/aos.css";

import ElementUI from "element-ui"; // quick UI implementation
import "element-ui/lib/theme-chalk/index.css";

import BackToTop from "vue-backtotop"; // back to top button

import VueI18n from "vue-i18n"; // locales

import VueAwesomeSwiper from 'vue-awesome-swiper'     // swipper - may not be using
import 'swiper/dist/css/swiper.css' // require styles

// use plugin
Vue.use(Router);
Vue.use(BootstrapVue);

Vue.use(ElementUI);
Vue.use(BackToTop);

Vue.use(VueI18n);
Vue.use(VueAwesomeSwiper);

Vue.config.productionTip = false;

// import views/component display in app
import Index from "./views/Index.vue";
import About from "./views/About.vue";
import Services from "./views/Services.vue";
import Media from "./views/Media.vue";
import Downlaod from "./views/Download.vue";

// import components for integration
// import Navbar from './components/navbar.vue' (for my future testing)

// const err = {
//   template:"<div>Error</div>"                (for my future testing)
// }

//define constant routes
const router = new Router({
  routes: [
    { path: "/", component: Index },
    { path: "/About", component: About },
    { path: "/Services", component: Services },
    { path: "/Media", component: Media },
    { path: "/Download", component: Downlaod },
    { path: "*", redirect: "/" }
  ]
});

// define language settings ( cannot use shorten the method ) ( vv will be using below shortened writing/coding method vv)
const messages = {
  English: require("./components/locales/en.json"),
  日本語: require("./components/locales/jp.json"),
  中文繁体字: require("./components/locales/cn(traditional).json"),
  中文简体字: require("./components/locales/cn(simplify).json")
};

const i18n = new VueI18n({
  locale: "English",
  messages
});

new Vue({
  // on create, initialize into the App
  created() {
    AOS.init();
  },
  i18n,
  //push router routes for to app as activation instructions
  router,
  render: h => h(App)
}).$mount("#app");
