Component: Navigation Bar

<template>
  <div class="navbar">
    <header>
      <div class="container">
        <b-navbar id="topNavMenu" toggleable="lg" fixed="top" class="bg-white">
          <div class="container">
            <b-navbar-brand>
              <img
                src="../imgs/bankLogo.png"
                class="d-inline-block img-fluid"
                alt="Bank_Logo"
                aria-label="Bank_Logo"
                style="width: 40px; height: 40px;"
                data-aos="zoom-in"
                data-aos-duration="300"
              />
              <strong
                class="NavBankName"
                data-aos="zoom-in-left"
                data-aos-anchor="#bank_anchor"
                data-aos-offset="10"
                data-aos-duration="1000"
              >&nbsp;AnyMultiBank</strong>
            </b-navbar-brand>

            <b-navbar-toggle target="nav-collapes"></b-navbar-toggle>

            <b-collapse id="nav-collapes" is-nav>
              <!-- navigation tab/menu -->
              <b-row class="mx-auto">
                <b-col md="12">
                  <!-- <b-navbar-nav class="mx-auto text-center"> -->
                  <b-card no-body class="transparentNavMenuBg">
                    <b-nav pills slot="header">
                      <b-nav-item to="/" data-aos="zoom-in-left" data-aos-duration="800">Home</b-nav-item>
                      <b-nav-item to="/About" data-aos="zoom-in-left" data-aos-duration="1000">About</b-nav-item>
                      <b-nav-item
                        to="Services"
                        data-aos="zoom-in-left"
                        data-aos-duration="1200"
                      >Services</b-nav-item>
                      <b-nav-item to="Media" data-aos="zoom-in-left" data-aos-duration="1400">Media</b-nav-item>
                      <b-nav-item
                        to="Download"
                        data-aos="zoom-in-left"
                        data-aos-duration="1600"
                      >Download</b-nav-item>
                    </b-nav>
                  </b-card>
                  <!-- </b-navbar-nav> -->
                </b-col>
              </b-row>

              <b-navbar-nav class="mx-auto text-center">
                <b-row>
                  <!-- support link -->
                  <b-col md="6">
                    <b-nav-item href="#" data-aos="zoom-in-left" data-aos-duration="1800">Support</b-nav-item>
                  </b-col>

                  <!-- language dropdown -->
                  <b-col md="6">
                    <b-nav-item-dropdown id="bank_anchor" text="EN" right>
                      <template slot="button-content" v-if="selected">
                        <span v-for="(lang,i) in langs" :key="i" :value="lang">
                          <span slot="text" v-if="lang == language">
                            <img
                              :src="require('./locales/locale_img/' + lang + '.png')"
                              width="21px"
                              height="14px"
                              class="img-fluid"
                            />
                            {{language}}
                          </span>
                        </span>
                      </template>
                      <template slot="button-content" v-else>
                        <span slot="text">{{this.$i18n.locale}}</span>
                      </template>

                      <!-- defines dropdown menu content -->
                      <b-dropdown-item
                        v-for="(lang,i) in langs"
                        :key="i"
                        :value="lang"
                        @click="languageSwitcher(lang)"
                      >
                        <img
                          :src="require('./locales/locale_img/' + lang + '.png')"
                          width="21px"
                          height="14px"
                          class="img-fluid"
                        />
                        {{lang}}
                      </b-dropdown-item>
                    </b-nav-item-dropdown>
                  </b-col>
                </b-row>
              </b-navbar-nav>
            </b-collapse>
          </div>
        </b-navbar>
      </div>
    </header>
  </div>
</template>

<script>
export default {
  name: "navbar",
  data() {
    return {
      // reusable language tracking object
      langs: ["日本語", "English", "中文繁体字", "中文简体字"],
      language: this.$t("language"),
      selected: false
    };
  },
  methods: {
    languageSwitcher(lang) {
      // langauge settings
      localStorage.setItem("lang", lang);

      this.$i18n.locale = lang;

      this.language = lang;

      // record tracking
      if (lang == "日本語") {
        var post_lang_jp = "jp";
        localStorage.setItem("post_lang", post_lang_jp);
      } else if (lang == "English") {
        var post_lang_en = "en";
        localStorage.setItem("post_lang", post_lang_en);
      } else if (lang == "中文繁体字") {
        var post_lang_cnT = "cnTraditional";
        localStorage.setItem("post_lang", post_lang_cnT);
      } else if (lang == "中文简体字") {
        var post_lang_cn = "cnSimplify";
        localStorage.setItem("post_lang", post_lang_cn);
      }

      this.selected = true;
    }
  },
  created() {
    //   on new start up, assign chosen langauge
    if (localStorage.getItem("lang") != null) {
      this.$i18n.locale = localStorage.getItem("lang");
      this.language = localStorage.getItem("lang");

      this.selected = true;
    }
  }
};
</script>


<style scoped>
.NavBankName {
  font-family: DinBold, -apple-system, Microsoft Yahei, 微软雅黑,
    MicrosoftJhengHei, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu,
    Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 20px;
  color: #352757;
}

.transparentNavMenuBg {
  background: transparent;
  border: none;
}

/* from web - css*/
.card-header {
  background-color: transparent;
  padding: 0.75rem 1.25rem;
  margin-bottom: 0;
  background: none;
  border-bottom: none;
}

.nav-pills .nav-link {
  color: black;
  border-radius: 0;
}

.nav-pills .nav-link:hover {
  color: darkblue;
  font-weight: bolder;
  font-family: DinBold, -apple-system, Microsoft Yahei, 微软雅黑,
    MicrosoftJhengHei, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu,
    Cantarell, Open Sans, Helvetica Neue, sans-serif;
  border-bottom: 3px solid coral;
  border-radius: 0;
}

/* checked around the web for vue router implemented class */
.nav-pills .nav-link.router-link-exact-active,
.nav-pills .show > .nav-link {
  color: grey;
  border-bottom: 3px solid lightseagreen;
  background-color: transparent;
}
/* ^^ from web ^^ */

/* languageSwitcher On Hover effects : Problem: this is not working*/
li#bank_anchor:hover > ul.dropdown-menu-right {
  display: block !important;
}
</style>

