<template>
  <div>
    <!-- Navigation Menu -->
    <Navbar></Navbar>

    <!-- main content : Media -->
    <main>
      <section
        :style="{'background-image' : 'url(' + require('../uploads/bgMedia.jpg') + ')'}"
        style="background-image: cover;"
        class="bg-fixed"
      >
        <div class="container internalSpacingTop">
          <b-row>
            <b-col sm="12">
              <div class="heading-title text-center">
                <h2>
                  <strong>{{$t('screenshotsTitle')}}</strong>
                </h2>
                <div class="divider"></div>
              </div>
            </b-col>
          </b-row>

          <!-- Note: picture location -->
          <b-row class="mt-5">
            <b-col md="12">
              <div class="container">
                <!-- swiper -->
                <md-card>
                  <md-card-media>
                    <swiper :options="swiperOption" class="text-center">
                      <swiper-slide>
                        <img
                          class="img-fluid imgSwiperSize"
                          src="../imgs/phone2.png"
                          alt="1st_screenshot"
                        />
                      </swiper-slide>
                      <swiper-slide>
                        <img
                          class="img-fluid imgSwiperSize"
                          src="../imgs/phone3.png"
                          alt="2st_screenshot"
                        />
                      </swiper-slide>
                      <swiper-slide>
                        <img
                          class="img-fluid imgSwiperSize"
                          src="../imgs/phone4.png"
                          alt="3st_screenshot"
                        />
                      </swiper-slide>
                      <swiper-slide>
                        <img
                          class="img-fluid imgSwiperSize"
                          src="../imgs/phone5.png"
                          alt="4st_screenshot"
                        />
                      </swiper-slide>
                      <div class="swiper-pagination" slot="pagination"></div>
                    </swiper>
                  </md-card-media>
                </md-card>
              </div>
            </b-col>
          </b-row>
        </div>
      </section>
    </main>
    <prefooter></prefooter>
    <Footer></Footer>
  </div>
</template>

<script>
// import related component
import Navbar from "../components/navbar";
import Prefooter from "../components/pre-footer";
import Footer from "../components/footer";

export default {
  name: "Media",
  data() {
    return {
      // For screenshots swiper
      swiperOption: {
        slidesPerView: 2,
        spaceBetween: 50,

        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        breakpoints: {
          1200: {
            slidesPerView: 3,
            spaceBetween: 200
          },
          992: {
            slidesPerView: 2,
            spaceBetween: 200
          },
          768: {
            slidesPerView: 1,
            spaceBetween: 200
          },
          576: {
            slidesPerView: 1,
            spaceBetween: 100
          }
        }
      }
    };
  },
  components: {
    Navbar,
    Prefooter,
    Footer
  }
};
</script>

<style scoped>
.swiper-slide {
  background-position: center;
  background-size: cover;
  height: auto;
  background: transparent;
}

.imgSwiperSize {
  width: auto;
  height: 580px;
}
</style>



