<template>
  <div id="app">
    <!-- loading not in use -->
    <loading
      :active.sync="isLoading"
      :can-cancel="true"
      :on-cancel="onCancel"
      :is-full-page="fullPage"
    ></loading>

    <router-view v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>
// Import component
import Loading from "vue-loading-overlay";
// Import stylesheet
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  provide() {
    return {
      reload: this.reload,
      loading: this.loading
    };
  },

  components: {
    Loading
  },

  data() {
    return {
      isRouterAlive: true,
      isloading: false,
      fullpage: true
    };
  },

  methods: {
    //define methods for the top script provide()-'return'
    // for forced page reload
    reload() {
      isRouterAlive: false;
      this.$nextTick(function() {
        this.isRouterAlive = true;
      });
    },

    // method to switch loading state.
    loading() {
      this.isloading = !this.isloading;
    }
  }
};
</script>


